.movies .movie {
  width: 520px;
  height: 300px;
  background-color: white;
  margin-bottom: 50px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 20px;
  color: #adaeb9;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.15),
    0 8px 16px -8px rgba(0, 0, 0, 0.2), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  border-radius: 5px;
}

.movie .movie_poster {
  max-width: 150px;
  margin-right: 30px;
  position: relative;
  top: -40px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie .movie_name,
.movie .movie_year {
  margin: 0;
  font-weight: 300;
}

.movie .movie_name {
  margin-bottom: 5px;
  font-size: 22px;
  color: #2c2c2c;
}

.movie .movie_genres {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 5px 0;
}

.movie_genres .genres_genre {
  margin-right: 10px;
  font-size: 14px;
}
